import React from "react";
import { Link } from 'react-router-dom';

function Nav() {
    return (
    <nav id="navbar">
        <h1 className="logo">
        <span className="text-primary">
            <i className="fas fa-book-open"></i> Sky</span> Web
        </h1>
        <ul>
            <Link to ="/">
                <li>Home</li>
            </Link>
            <Link to ="/about">
                <li>About</li>
            </Link>
            <Link to ="/contact">
                <li>Contact</li>
            </Link>
        </ul>
    </nav>
    );
}

export default Nav;